import React from "react";
import { Link } from "react-router-dom";

export default ({ pages }) => {
  const renderPages = () => {
    return pages.map((p, i) => {
      return p.visible === false ? null : (
        <li className="nav-item" key={i + 1}>
          <Link className="nav-link" key={i + 1} to={p.path}>
            {p.label ? p.label : p.path}
          </Link>
        </li>
      );
    });
  };
  return (
    <>
      <ul className="nav nav-pills flex-column sidebar">{renderPages()}</ul>
    </>
  );
};
