import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Header from "../components/Header";
import UbootParser from "./UbootParser";
import Dload from "./Dload";
import Sidemenu from "../components/Sidemenu";
import Md5Page from "./Md5Page";
import ChangePass from "./ChangePass";

const pages = [
  {
    path: "/",
    component: UbootParser,
    label: "Uboot",
    exact: true
  },
  {
    path: "/dload",
    component: Dload,
    label: "Dload",
    exact: true
  },
  {
    path: "/md5",
    component: Md5Page,
    label: "MD5",
    exact: true
  },
  {
    path: "/change-pass",
    component: ChangePass,
    label: "Change pass",
    exact: true
  }
];

export default () => {
  return (
    <div className="App">
      <div className="container">
        <Router basename={process.env.PUBLIC_URL}>
          <div className="row">
            <Header />
          </div>
          <div className="row">
            <div className="col-3">
              <Sidemenu pages={pages}></Sidemenu>
            </div>
            <div className="col-8">
              <Switch>
                {pages.map((pg, i) => (
                  <Route {...pg} key={i + 1} />
                ))}
              </Switch>
            </div>
          </div>
        </Router>
      </div>
    </div>
  );
};
